/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.edge-woo-pagination-holder {
    @include edgeRelativeHolderLayout();
    clear: both;
    padding: 36px 0 90px;
    width: 100%;
    vertical-align: middle;

    .edge-woo-pagination-inner {
        position: relative;
        display: table;
        text-align: center;
        width: 100%
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            display: inline-block;
            vertical-align: top;
            margin: 0;

            a,
            span {
                position: relative;
                display: inline-block;
	            width: auto;
                margin: 0 7px;
                padding: 0;
                vertical-align: top;
                font-family: $default-heading-font;
                font-weight: 500;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                color: #000;
                @include edgeTransition(color .2s ease-out);

                &:hover,
                &.current {
                    color: $first-main-color;
                }
            }

            a {

                &.next,
                &.prev {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    top: -2px;
                    font-size: 18px;
                    line-height: 1;
                    margin: 0 6px 0 4px;

                    &:before {
                        color: #fff;
                        position: relative;
                        top: 3px;
                        display: block;
                        font-size: 17px;
                        line-height: inherit;
                        @include edgeTransition(color .2s ease-out);
                    }

                    &:hover:before {
                        color: $first-main-color;
                    }
                }
    
                &.prev {
                }
    
                &.next {
                }
            }
        }
    }
}

.edge-woo-view-all-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 1%;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 2px;
        color: $default-text-color;

        &:before {
            content: '|';
            padding: 0 14px 0 0;
            color: $default-text-color;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */