/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.edge-woocommerce-page {

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 40px 30px;
        margin: 0 0 30px;
        line-height: 38px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }

        .woocommerce-Button,
        .button  {
            position: absolute!important;
            right: 29px;
            top: 50%;
            @include edgeTransform( translateY(-50%) );
        }
    }

    .woocommerce-info {

        .showcoupon,
        .showlogin {
            color: $default-text-color;
            font-weight: 400;

            &:hover {
                color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */