/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edge-pl-holder {
    @include edgeRelativeHolderLayout();
	
	&.edge-info-on-image {
		
		.edge-pli {
			
			.edge-pli-add-to-cart {
				position: absolute;
				display: block;
				top: auto;
				bottom: 0;
				left: 0;
				height: auto;
				padding: 0;
				@include edgeTransform(translateY(100%));
				@include edgeTransition(transform 0.3s, opacity 0.25s);
				width: 100%;
				text-align: center;
				z-index: 50;

				.button {
					@include edgeButtonDefaultStyle();
					@include edgeButtonMediumParams();
					@include edgeButtonSolidColor();
					width: 100%;

					> i {
						@include edgeTransitionTransform(0.2s $default-easing);
					}

					&:hover {
						> i {
							@include edgeTransform(translate3d(3px,0,0));
						}
					}
				}
			}

			.edge-pli-inner {

				.edge-pli-text-outer {
					opacity: 0;
					@include edgeTransition(opacity 0.25s);
				}

				.edge-pli-text-inner {
					margin: 25px 0 0;
					@include edgeTransitionTransform(.25s ease);
					@include edgeTransform(translate3d(0,40px,0));
				}
			}

			&:hover {

				.edge-pli-add-to-cart {
					@include edgeTransform(none);
				}

				.edge-pli-inner {

					.edge-pli-text-outer {
						opacity: 1;
					}

					.edge-pli-text-inner {
						@include edgeTransitionTransform(.45s $default-easing-img);
						@include edgeTransform(translate3d(0,0,0));
					}
				}
			}
		}
	}

	&.edge-info-below-image {
		.edge-pli-title {
			a:hover {
				color:#130a02;
			}
		}

		.edge-pli-inner {

			.edge-pli-text {
				position: absolute;
				display: block;
				top: auto;
				bottom: 0;
				left: 0;
				height: auto;
				padding: 0;
				@include edgeTransform(translateY(100%));
				@include edgeTransition(transform 0.3s, opacity 0.25s);
				z-index: 50;

				.edge-pli-add-to-cart {

					a {
						width: 100%;
					}
				}
			}
		}

		.edge-pli-title-wrapper {
			position: relative;

			.edge-pli-price {
				position: absolute;
				right: 0;
				top: 50%;
				@include edgeTransform(translateY(-50%));
			}

			.edge-pli-title {
				padding: 0 140px 0 0;
			}
		}

		.edge-pli {

			.edge-pli-category {
				margin: 0;

				a {
					color: #000;
					font-weight: 400;

					&:hover {
						color: $first-main-color;
					}
				}
			}

			.edge-pli-text-wrapper {
				margin: 22px 0 0;
			}

			&:hover {

				.edge-pli-inner {

					.edge-pli-text {
						color: #fff !important;
						@include edgeTransform(none);
					}
				}
			}
		}
	}

    .edge-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

	    .edge-pli-title {
		    margin: 0 0 4px;
	    }
	
	    .edge-pli-category {
		    margin: 11px 0 3px;
	    }

        .edge-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .edge-pli-rating-holder {
		    @include edgeRelativeHolderLayout();
		    line-height: 1;
		    margin: 0px 0 13px;
	    }

	    .edge-pli-rating {
		    @include edgeWooCommerceRatingsStyle();
	    }

        .edge-pli-price {
	        @include edgeWooCommercePriceStyle();
        }

		&:hover {

			.edge-pli-inner {

				.edge-pli-image {

					img {
						@include edgeTransitionTransform(.45s $default-easing-img);
						@include edgeTransform(scale(1.07));
					}
				}

				.edge-pli-link {
					&:after {
						opacity: 1;
					}
				}
			}
		}
    }

    .edge-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;
		box-shadow: 0px 0px 9px 0px rgba(0, 0, 0,0.07);

        .touch & {
            cursor: pointer;
        }

        .edge-pli-image {
            @include edgeRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
				@include edgeTransitionTransform(.2s ease);
            }

            .edge-pli-onsale,
            .edge-pli-out-of-stock,
            .edge-pli-new-product {
	            @include edgeWooCommerceMarksGeneralStyle();
            }

            .edge-pli-onsale {
	            @include edgeWooCommerceSaleStyle();
            }

            .edge-pli-out-of-stock {
	            @include edgeWooCommerceOutOfStockStyle();
            }
	
	        .edge-pli-new-product {
		        @include edgeWooCommerceNewProductStyle();
	        }
        }
            
        .edge-pli-text {
            @include edgeAbsoluteHolderLayout();
            padding: 10px;
            box-sizing: border-box;
        }

        .edge-pli-text-outer {
	        @include edgeTableLayout();
	        text-align: center;
			z-index: 3;
        }

        .edge-pli-text-inner {
            @include edgeTableCellLayout();

            .edge-pli-title a,
            .edge-pli-category a,
            .edge-pli-excerpt,
            .edge-pli-rating,
            .edge-pli-price {
				color: #fff;

				&:hover {
					color: #fff;
				}
			}

	        .edge-pli-add-to-cart {

		        &.edge-default-skin {

			        .button,
			        .added_to_cart {
				        @include edgeButtonSolidColor();

						> i {
							@include edgeTransitionTransform(0.2s $default-easing);
						}

				        &:hover {
				            @include edgeButtonSolidHoverColor();

							> i {
								@include edgeTransform(translate3d(3px,0,0));
							}
				        }
			        }
		        }

		        &.edge-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.edge-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include edgeButtonDefaultStyle();
	            @include edgeButtonMediumParams();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .edge-pli-link {
		    @include edgeAbsoluteHolderLayout();

			&:after {
				content:'';
				position:absolute;
				top:0;
				left:0;
				height:100%;
				width:100%;
				opacity:0;
				background-color: rgba($default-heading-color, 0.59);
				@include edgeTransition(opacity .2s ease-in-out);
			}
	    }
    }

    .edge-pli-text-wrapper {
        @include edgeRelativeHolderLayout();
        margin: 22px 0 8px;
        text-align: left;
    }
	
	&.edge-product-info-light {
		
		.edge-pli-inner {
			
			.edge-pli-text-inner {
				
				.edge-pli-title,
				.edge-pli-category a,
				.edge-pli-excerpt,
				.edge-pli-rating,
				.edge-pli-price {
					color: #fff;
				}
				
				.edge-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.edge-product-info-dark {
		
		.edge-pli-inner {
			
			.edge-pli-text-inner {
				
				.edge-pli-title,
				.edge-pli-category a,
				.edge-pli-excerpt,
				.edge-pli-rating,
				.edge-pli-price {
					color: $default-heading-color;
				}
				
				.edge-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Standard Layout - begin ***************/
	
	&.edge-standard-layout {
		
		.edge-pli {
			float: left;
		}
		
		&.edge-large-space {
			margin: 0 0 -50px;
		}
		
		&.edge-normal-space {
			margin: 0 0 -30px;
		}
		
		&.edge-small-space {
			margin: 0 0 -20px;
		}
		
		&.edge-tiny-space {
			margin: 0 0 -10px;
		}
		
		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($standard_list_columns) {
			&.edge-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.edge-pli {
					width: $column_width;
					
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	
	/*************** Standard Layout - end ***************/
	
	/*************** Masonry Layout - begin ***************/
	
	&.edge-masonry-layout {
		
		.edge-pl-outer {
			opacity: 0;
		}
		
		.edge-pl-sizer {
			width: 100%;
		}
		
		.edge-pl-gutter {
			width: 0;
		}
		
		.edge-pli-inner {
			width: 100%;
			height: 100%;
			
			.edge-pli-image {
				height: 100%;
				
				img {
					height: 100%;
				}
			}
		}
		
		.edge-pli.edge-woo-image-large-width {
			
			img {
				width: 100%;
			}
		}
		
		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_list_columns) {
			&.edge-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.edge-pli,
				.edge-pl-sizer {
					width: $column_width;
				}
				
				.edge-pli.edge-woo-image-large-width,
				.edge-pli.edge-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */