/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin edgeWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: 100px;
    height: 50px;
    margin: 0;
    padding: 0 32px;
    color: #fff;
    font-family: $default-heading-font;
    font-size: 12px;
    font-weight: 700;
    line-height: 50px;
    letter-spacing: 0.1em;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    text-transform: uppercase;
    @include edgeTransform(translateZ(0));
}

@mixin edgeWooCommerceSaleStyle() {
    right: 0;
    background-color: $yellow-color;
    color: $default-heading-color;
}

@mixin edgeWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: $default-dark-color;
}

@mixin edgeWooCommerceNewProductStyle() {
    right: 0;
    background-color: $second-main-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin edgeWooCommercePriceStyle() {
    position: relative;
    display: block;
    line-height: 1;
    color: $dark-skin-color;
    font-family: $default-heading-font;
    font-size: 18px;
    font-weight: 700;

    * {
        font-size: inherit;
        line-height: inherit;
    }

    del,
    ins {
        display: inline-block;
        vertical-align: middle;
    }

    del {
        margin: 0;
    }

    ins {
        text-decoration: none;
    }
}

/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "FontAwesome";
    font-size: 15px;
    line-height: 1;
    letter-spacing: 4px;
    color: $default-heading-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin edgeWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;

    &:before {
        content: "\f006\f006\f006\f006\f006";
    }

    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        &:before {
            content: "\f005\f005\f005\f005\f005";
        }
    }
}

@mixin edgeWooCommerceSingleProductRatingsStyle() {

    &:before {
        content: "\f006";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
}

/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */