/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edge-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .edge-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edge-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
		color: #000;

        .edge-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;

            .edge-cart-number {
                height: 15px;
                width: 15px;
                line-height: 15px !important; //!important rule to override header icons style
                text-align: center;
                background-color: transparent;
                font-size: 11px;
                color: $default-dark-color;
                border-radius: 50%;
                display: block;
                position: absolute;
                top: -10px;
                right: -12px;
                @include edgeTransition(all .3s ease);
            }

        }

        &.edge-header-cart-icon-pack {

            @include edgeTransition(all .3s ease);

            .edge-cart-icon {
                font-size: 23px;
                line-height: 1em;

                * {
                    display: block;
                    line-height: inherit;
                    color: currentColor;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }

            &:hover {
                color: $first-main-color;

                .edge-cart-icon .edge-cart-number {
                    background-color: $default-dark-color;
                    color: $header-light-color;
                }
            }
        }

        &.edge-header-cart-svg-path {

            .edge-cart-icon {

                > * {
                    vertical-align: middle;
                }

                > *,
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .edge-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.edge-dark-header .edge-page-header > div:not(.edge-sticky-header):not(.fixed) .edge-shopping-cart-holder .edge-header-cart,
.edge-dark-sticky-header .edge-page-header > .edge-sticky-header:not(.fixed) .edge-shopping-cart-holder .edge-header-cart {
    color: $default-dark-color;

    &:hover {
        color: $default-text-color;

        .edge-cart-number {
            color: $header-light-color;
            background-color: $default-dark-color;
        }
    }
}

.edge-light-header .edge-page-header > div:not(.edge-sticky-header):not(.fixed) .edge-shopping-cart-holder .edge-header-cart,
.edge-light-sticky-header .edge-page-header > .edge-sticky-header:not(.fixed) .edge-shopping-cart-holder .edge-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-color;

        .edge-cart-number {
            color: $default-dark-color;
            background-color: $header-light-color;
        }
    }
}

.edge-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 332px;
    background-color: $default-dark-color;
    color: #fff;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    box-sizing: border-box;
    @include edgeTransition(all 0.35s cubic-bezier(.53, .01, .18, 1));

    ul {
        margin: 38px 32px 40px;
        padding: 0;

        li {
            @include edgeTableLayout();
            padding: 14px 0;
            box-sizing: border-box;

            &.edge-empty-cart {
                color: #fff;
                border-bottom: 0;
	            margin-bottom: 30px;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edge-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .edge-item-info-holder {
        @include edgeTableCellLayout();
        padding-left: 20px;

        .edge-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color: #fff;
            font-size: 14px;

            a:hover {
                color: rgba(#fff,.7);
            }
        }

        .remove {
            position: absolute;
            top: calc(50% - 26px);
            right: -12px;
            font-size: 28px;
            line-height: 1;
            color: #fff;
            @include edgeTransform(translateY(-50));

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edge-quantity {
            display: block;
            font-size: 14px;
        }
    }

    .edge-cart-bottom {
        padding: 20px 0 0;

        .edge-subtotal-holder {
            position: relative;
            width: 100%;
            margin: 0 0 31px;
            font-family: $default-heading-font;
            font-weight: 700;

            > * {
                color: #fff;
                line-height: 20px;
            }

            .edge-total {
                font-size: 18px;
                text-transform: uppercase;
            }

            .edge-total-amount {
                margin-left: 6px;
                font-size: 23px;
            }
        }

        .edge-btn-holder {
            position: relative;
        }

        .edge-view-cart,
        .edge-checkout {
            position: relative;
            display: inline-block;
            height: 47px;
            font-family: $default-heading-font;
            font-size: 14px;
            font-weight: 500;
            background: #fff;
            color: #000;
            padding: 10px 25px;
            text-transform: uppercase;
            text-align: center;
            box-sizing: border-box;
            @include edgeTransition(color .2s ease-out, background-color .2s ease-out);
            margin-right: 16px;

            &:after {
                display: inline-block;
                font-family: FontAwesome;
                content: '\f101';
                font-size: 18px;
                margin-left: 5px;
            }

            &:last-child {
                margin-right: 0;
            }

            &:hover {
                color: rgba(#000,.7);
            }
        }

        .edge-view-cart .edge-icon-element {
            font-size: 20px;
        }

        .edge-checkout .edge-icon-element {
            font-size: 18px;
        }
    }
}

.edge-vertical-menu-area {

    .edge-shopping-cart-dropdown {
        display: none !important;
    }
}

/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */