/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .edge-onsale,
    .edge-out-of-stock,
    .edge-new-product {
        @include edgeWooCommerceMarksGeneralStyle();
    }
    
    .edge-onsale {
        @include edgeWooCommerceSaleStyle();
    }
    
    .edge-out-of-stock {
        @include edgeWooCommerceOutOfStockStyle();
    }

    .edge-new-product {
        @include edgeWooCommerceNewProductStyle();
    }
}

.edge-woo-single-page {
    
    .edge-single-product-content {
        
        .edge-onsale {
            right: 15px;
        }
        
        .edge-out-of-stock,
        .edge-new-product {
            left: 15px;
        }
    }
    
    &.edge-woo-single-thumb-on-left-side {
        
        .edge-single-product-content {
            
            .edge-onsale {
                left: 120px;
            }
            
            .edge-out-of-stock,
            .edge-new-product {
                right: 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */