/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.edge-woo-single-page {

    .edge-content .edge-content-inner>.edge-container>.edge-container-inner,
    .edge-content .edge-content-inner>.edge-full-width>.edge-full-width-inner {
        padding-top: 144px;
    }

    &.edge-woo-single-thumb-on-left-side {

        .edge-single-product-content {

            .images {

                @media only screen and (min-width: $ipad-portrait-plus-pixel) {

                    figure {
                        width: 100%;
                        margin: 0;
                    }

                    .woocommerce-product-gallery__image {
                        box-shadow: 0 0 10px 3px rgba(0,0,0,.07);


                        &:first-child {
                            left: 120px;
                            width: calc(100% - 120px);
                        }
                    }

                    &.woocommerce-product-gallery--with-images {

                        .woocommerce-product-gallery__image {
                            box-shadow: 0 0 10px 3px rgba(0,0,0,.07);

                            &:not(:first-child) {
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100px;
                                height: calc(25% + 5px);
                                padding: 0 0 20px;
                                box-sizing: border-box;

                                a {
                                    height: 100%;

                                    img {
                                        height: 100%;
                                    }
                                }
                            }

                            $thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
                            @for $i from 0 to length($thumbnail_numbers) {
                                $thumbnail_number_value: nth($thumbnail_numbers, $i+1);

                                &:nth-child(#{$thumbnail_number_value}#{n}) {
                                    $first_param: (25 * $i)#{'%'};
                                    $second_param: (5 * $i)#{px};
                                    $topPos: calc(#{$first_param} + #{$second_param});

                                    top: $topPos;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .woocommerce-review__author {
        font-weight: 400;
        text-transform: uppercase;
    }

    .product {
        @include edgeRelativeHolderLayout();
        float: none;
        margin: 0 0 66px;
    }

    .edge-single-product-content {
        @include edgeRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;

        .images {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            width: 52%;
            margin: 0;
            padding: 0 29px 0 0;
            float: left;
            box-sizing: border-box;

            figure {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 -15px;

                a {
                    @include edgeRelativeHolderLayout();

                    img {
                        position: relative;
                        width: 100%;
                        display: block;
                    }
                }
            }

            .woocommerce-product-gallery__trigger {
                position: absolute;
                top: 10px;
                right: 39px;
                width: 30px;
                height: 30px;
                line-height: 30px;
                text-align: center;
                z-index: 10;
            }

            .woocommerce-product-gallery__image {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 15px;
                box-sizing: border-box;

                &:first-child {
                    left: 15px;
                    width: calc(100% - 30px);
                    padding: 0;
                }

                .zoomImg {
                    cursor: crosshair;
                }
            }

            &.woocommerce-product-gallery--with-images {

                .woocommerce-product-gallery__image {

                    &:not(:first-child) {
                        float: left;
                        padding-top: 30px;
                    }
                }
            }

            $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
            @for $i from 0 to length($thumb_3_columns_label) {
                &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                    $column_width: 100%/($i+2);

                    .woocommerce-product-gallery__image {

                        &:not(:first-child) {
                            width: $column_width;
                        }
                    }
                }
            }
        }
    }

    .edge-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 44%;
        padding: 0 0 0 4%;
        float: left;
        box-sizing: border-box;

        .edge-single-product-title {
            display: block;
            margin: 0;
        }

        .price {
            @include edgeWooCommercePriceStyle();
            margin: 16px 0 10px;
            font-size: 30px;
            color: #000;
        }

        .edge-social-title {
            display: block;
            margin: 0 0 22px;
            font-family: $default-heading-font;
            font-size: 30px;
            font-weight: 700;
            text-transform: uppercase;
        }

        .single_add_to_cart_button {
            background-color: $first-main-color !important;
            color: #fff !important;
            padding: 11px 30px !important;

            &:after {
                content: "\f101";
                position: relative;
                display: inline-block;
                margin: 0 0 0 7px;
                bottom: 1px;
                font-size: 15px !important;
                font: normal normal normal 14px/1 FontAwesome;
                text-rendering: auto;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                @include edgeTransitionTransform(0.2s $default-easing);
            }

            &:hover {
                &:after {
                    @include edgeTransform(translate3d(3px,0,0));
                }
            }
        }

        .woocommerce-product-rating {
            @include edgeRelativeHolderLayout();
            line-height: 1;
            margin-top: 14px;

            .woocommerce-review-link {
                line-height: 26px;
                font-weight: 400;
            }
        }

        .woocommerce-product-details__short-description {
            @include edgeRelativeHolderLayout();
            margin: 25px 0 0;

            p {
                margin: 0;
            }
        }

        .product_meta {
            margin: 68px 0 0;

            > h5 {
                margin-bottom: 14px;
            }

            > span {
                display: block;
                margin: 3px 0;
                color: $default-heading-color;
                font-weight: 700;

                span {
                    color:$default-heading-color;
                    font-weight: 400;
                }

                a {
                    color: $default-heading-color;
                    font-weight: 400;

                    &:hover {
                        color: rgba($default-heading-color, .7);
                    }
                }
            }
        }

        .edge-woo-social-share-holder {
            @include edgeRelativeHolderLayout();
            margin: 62px 0 0;

            > * {
                display: table-cell;
                vertical-align: top;
            }

            > span {
                padding: 0 9px 0 0;
            }
        }

        p.stock.out-of-stock,
        p.stock.in-stock {
            color: $default-heading-color;
            font-weight: 400;
            margin: 18px 0 0;
        }

        form.cart {
            @include edgeRelativeHolderLayout();
            margin: 28px 0 0;
        }

        .edge-quantity-buttons {
            margin: 0 15px 0 0;
        }
    }

    .woocommerce-tabs {
        @include edgeRelativeHolderLayout();
        margin: 58px 0 0;

        ul.tabs {
            @include edgeRelativeHolderLayout();
            list-style: none;
            margin: 0;
            padding: 0;
            clear: both;

            > li {
                position: relative;
                display: inline-block;
                float: left;
                vertical-align: middle;

                &.active a:after {
                    background-color: $first-main-color;
                }

                a {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    padding: 2px 0 2px 0;
                    margin-right: 43px;
                    font-family: $default-heading-font;
                    font-size: 18px;
                    font-weight: 700;
                    color: $default-heading-color;
                    text-transform: uppercase;

                    &:after {
                        content: '';
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        height: 2px;
                        width: 100%;
                    }
                }
            }
        }

        .entry-content {
            @include edgeRelativeHolderLayout();

            > h2 {
                display: none;
            }

            > p {
                margin: 25px 0 27px;
            }
        }

        table {
            margin: 21px 0 27px;

            tr, th {
                border: 0;
            }

            td, th {
                padding: 3px 0;
                vertical-align: middle;
                text-align: left;

                p {
                    margin: 0;
                }
            }

            th {
                color: $default-heading-color;
                font-weight: 700;
                width: 1%;
                padding: 0 10px 0 0;
            }

            td {
                width: 99%;
            }
        }

        #reviews {
            @include edgeRelativeHolderLayout();
            margin: 21px 0 33px;

            h2 {
                font-size: 16px;
                line-height: 24px;
                margin: 8px 0 24px;
            }

            ol.commentlist {
                list-style: none;

                li {
                    margin: 0 0 15px;

                    &:last-child {
                        margin: 0;
                    }
                }

                .comment_container {
                    @include edgeRelativeHolderLayout();

                    img {
                        float: left;
                        width: 83px;
                    }
                }

                .comment-text {
                    padding: 0 0 0 102px;

                    p {
                        margin: 0;
                    }
                }
            }

            .comment-respond {
                margin-top: 15px;

                .comment-reply-title {
                    margin: 0 0 10px;
                }

                p {
                    margin: 0;

                    > label {
                        display: block;
                        font-family: $default-heading-font;
                        font-size: 16px;
                        line-height: 24px;
                        margin: 8px 0 24px;
                        font-weight: 700;
                        text-transform: uppercase;
                    }
                }

                .stars {
                    overflow: hidden;

                    a {
                        position: relative;
                        display: inline-block;
                        color: transparent;
                        font-size: 0;
                        margin: 0;
                        @include edgeWooCommerceSingleProductRatingsStyle();
                        font-weight: 400;
                    }

                    &.selected a {
                        &:before {
                            content: '\f005';
                        }

                        &.active ~ a:before {
                            content: "\f006"
                        }
                    }
                }
            }
        }
    }

    .related.products,
    .upsells.products {
        margin: 31px 0 0;

        > h2 {
            display: block;
            font-family: $default-heading-font;
            font-size: 30px;
            font-weight: 700;
            margin: 20px 0 35px
        }

        .product {

            .edge-pl-text-wrapper {
                margin-bottom: 0;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */