/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edge-content,
div.woocommerce {

    .edge-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .edge-quantity-input,
        .edge-quantity-minus,
        .edge-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-dark-color;
            background-color: transparent;
            border: 1px solid $default-dark-color;
            text-align: center;
            box-sizing: border-box;
        }

        .edge-quantity-input {
            position: relative;
            height: 54px;
            width: 52px;
            padding: 0;
            margin: 0 27px 0 0;
            font-family: $default-heading-font;
            font-size: 18px;
            font-weight: 700;
            line-height: 42px;
            outline: 0;
        }

        .edge-quantity-minus,
        .edge-quantity-plus {
            position: absolute;
            right: 0;
            width: 27px;
            height: 27px;
            font-size: 18px;
            font-weight: 700;
            line-height: 24px;
            border-left: 0;
            cursor: pointer;
            color: $default-dark-color;
            @include edgeTransition(color .15s ease-in-out);

            &:hover {
                color: $gray-hover-color;
            }
        }

        .edge-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .edge-quantity-minus {
            bottom: 0;
            border-top: 1px solid #000;
        }
    }
}

.woocommerce-page .edge-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
        margin: 0 0 45px;
        padding: 10px 0 7px;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */