/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.edge-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.edge-content .woocommerce {
    @include edgeRelativeHolderLayout();
}

.edge-pl-main-holder {
    margin: 0 0 22px;

    .edge-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;

        ul.products>.product {

            .edge-pl-inner {

                .button,
                .added_to_cart {
                    @include edgeButtonDefaultStyle();
                    @include edgeButtonMediumParams();
                    @include edgeButtonSolidColor();
                    position: absolute;
                    display: block;
                    top: auto;
                    bottom: 0;
                    left: 0;
                    height: auto;
                    width: 100%;
                    z-index: 50;
                    @include edgeTransform(translateY(100%));
                    @include edgeTransition(transform 0.3s, opacity 0.25s);

                    &:after {
                        display: inline-block;
                        font-family: 'FontAwesome';
                        content: '\f101';
                        font-size: 18px;
                        margin-left: 5px;
                        @include edgeTransitionTransform(0.2s $default-easing);
                        position: relative;
                        background-color: transparent;
                        width: auto;
                        height: auto;
                        color: #fff;
                        opacity: 1;
                    }
                }

                .edge-pl-text-outer {
                    @include edgeTableLayout();
                    text-align: center;
                    z-index: 15;
                    opacity: 0;
                    @include edgeTransition(opacity 0.25s);
                }

                .edge-pl-text-inner {
                    @include edgeTableCellLayout();
                    @include edgeTransitionTransform(.25s ease);
                    @include edgeTransform(translate3d(0,40px,0));

                    .edge-product-list-title a {
                        color: #fff;

                        &:hover {
                            color: currentColor;
                        }
                    }
                }
            }

            &:hover {

                .edge-pl-inner {

                    .button,
                    .added_to_cart {
                        @include edgeTransform(none);
                    }

                    .edge-pl-text-outer {
                        opacity: 1;
                    }

                    .edge-pl-text-inner {
                        @include edgeTransitionTransform(.45s $default-easing-img);
                        @include edgeTransform(translate3d(0,0,0));
                    }
                }
            }
        }
    }
}

.edge-woo-pl-info-below-image {

    .edge-woo-title-holder {
        position: relative;

        .edge-product-list-title {
            padding: 0 150px 0 0;
        }
    }

    ul.products>.product {

        .price {
            position: absolute;
            top: 50%;
            right: 0;
            @include edgeTransform(translateY(-50%));
        }
    }
}

ul.products {
    list-style: none;
    margin: 0 -15px;
    clear: both;

    > .product {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        float: left;
        margin: 0 0 30px;
        padding: 0 15px;
        box-sizing: border-box;

        .touch & {
            cursor: pointer;
        }

        &:hover {

            .edge-pl-inner {

                .edge-pl-image {

                    img {
                        @include edgeTransitionTransform(.45s $default-easing-img);
                        @include edgeTransform(scale(1.07));
                    }
                }

                > a {
                    &:after {
                        opacity:1;
                    }
                }

                .edge-pl-text-inner {
                    @include edgeTransform(none);
                }
            }
        }

        .edge-product-list-title {
            margin: 0 0 4px;
            text-transform: uppercase;
        }

        .edge-woo-category-list {

            .edge-woo-category-title {

                a {
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 1.2em;
                    margin: 0 5px 20px 0;
                }
            }
        }

        .edge-pl-rating-holder {
            display: none;
        }

        .price {
            @include edgeWooCommercePriceStyle();
        }

        .button,
        .added_to_cart {
            margin: 25px 0 0;
            color: #fff;
            text-align: center;
            border: 0;

            &:after {
                display: inline-block;
                font-family: 'FontAwesome';
                content: '\f101';
                font-size: 18px;
                margin-left: 5px;
                @include edgeTransitionTransform(0.2s $default-easing);
            }

            &:hover {
                border: 0;
                color: #fff;

                &:after {
                    @include edgeTransform(translate3d(3px,0,0));
                }
            }

            .edge-woo-pl-info-below-image & {
                margin: 0;
            }
        }

        .button.added {
            display: none;
        }

        .edge-pl-inner {
            @include edgeRelativeHolderLayout();
            overflow: hidden;

            .edge-pl-image {
                @include edgeRelativeHolderLayout();
                box-shadow: 0 0 10px 3px rgba(0,0,0,.07);
                overflow: hidden;

                img {
                    display: block;
                    width: 100%;
                    @include edgeTransitionTransform(.2s ease);
                }
            }

            .edge-pl-text {
                @include edgeAbsoluteHolderLayout();
                box-sizing: border-box;

                &:after {
                    content:"";
                    @include edgeAbsoluteHolderLayout();
                    opacity:0.6;
                    z-index:10;
                }
            }

            .edge-pl-text-outer {
                position: relative;
                z-index:20;
                height: 100%;
            }

            .edge-pl-text-inner {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: auto;
                @include edgeTransform(translateY(100%));
                @include edgeTransition(transform 0.3s, opacity 0.25s);

                a {
                    width: 100%;
                }

                .edge-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }

                .price del {
                    color: rgba(#fff, .8);
                }
            }

            > a {
                @include edgeAbsoluteHolderLayout();

                &:after {
                    content:'';
                    position:absolute;
                    top:0;
                    left:0;
                    height:100%;
                    width:100%;
                    opacity:0;
                    background-color: rgba($default-heading-color, 0.59);
                    @include edgeTransition(opacity .2s ease-in-out);
                }
            }
        }

        .edge-pl-text-wrapper {
            @include edgeRelativeHolderLayout();
            margin: 22px 0 1px;

            .edge-product-list-title a:hover {
                color:#130a02;
            }
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large', 'huge' );
$columns_space_width: (0, 5, 10, 15, 20, 25, 33);

@for $i from 0 to length($columns_space_label) {
    &.edge-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width, $i+1);
        $column_width_margin: $column_width * 2;

        ul.products {
            @if ($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }

            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.edge-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {

    $product_list_columns: ('columns-3', 'columns-4');

    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);

        > .products {

            > .product {

                @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                    .edge-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;

                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.related.products .product .button {

}

.woocommerce {

    &.columns-1 {

        > .products {

            > .product {
                float: none;
                width: 100%;
            }
        }
    }

    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');

    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);

            > .products {

                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};

                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (min-width: 1025px) {
    .edge-woocommerce-columns-2 .cross-sells>.products>.product,
    .edge-woocommerce-columns-2 .edge-pl-main-holder>.products>.product,
    .edge-woocommerce-columns-2 .related.products>.products>.product,
    .edge-woocommerce-columns-2 .upsells.products>.products>.product {
        width: 50%;
    }
}

@media only screen and (max-width: 680px) {
    .edge-woocommerce-columns-2 .cross-sells>.products>.product,
    .edge-woocommerce-columns-2 .edge-pl-main-holder>.products>.product,
    .edge-woocommerce-columns-2 .related.products>.products>.product,
    .edge-woocommerce-columns-2 .upsells.products>.products>.product {
        width: 100%;
    }
}

/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */