/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .edge-content,
div.woocommerce {

    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.edge-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include edgeButtonDefaultStyle();
        @include edgeButtonMediumParams();
        color: #fff;
        background-color: $first-main-color;
        border: 2px solid $first-main-color;
        padding: 14px 30px;
        cursor: pointer;
        z-index: 3;

        &:after {
            @include edgeTransitionTransform(0.2s $default-easing);
        }

        &:hover {
            color: #fff;
            background-color: $default-dark-color;
            border: 2px solid $default-dark-color;

            &:after {
                @include edgeTransform(translate3d(3px,0,0));
            }
        }
    }

    .price_slider_amount button[type="submit"]:not(.edge-woo-search-widget-button){
        @include edgeButtonDefaultStyle();
        @include edgeButtonMediumParams();
        @include edgeButtonSolidColor();

        &:after {
            display: inline-block;
            font-family: 'FontAwesome';
            content: '\f101';
            font-size: 18px;
            margin-left: 5px;
            @include edgeTransitionTransform(0.2s $default-easing);
        }

        &:hover {
            @include edgeButtonSolidHoverColor();

            &:after {
                @include edgeTransform(translate3d(3px,0,0));
            }
        }
    }

    a.button.added {
        display: none;
    }
}

.edge-pli-add-to-cart  {

    i {
        font-size: 18px;
    }
}

/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */