/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {

        .edge-widget-title {
            margin-bottom: 11px;
        }

        ul {
            padding: 0;
            margin: 0;
            list-style: none;

            li {
                @include edgeRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;

                a {
                    color: $default-text-color;
                }

                ul {
                    margin: 14px 0 0 10px;
                }
            }

            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    color: #000;
                    font-family: $default-heading-font;
                    font-weight: 700;
                    text-transform: uppercase;
                    @include edgeTransition(padding .2s ease);

                    &:after {
                        position: absolute;
                        display: block;
                        content: " ";
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        border-bottom: 1px solid #000;
                    }

                    &:hover {
                        color: rgba(#000, 0.7);
                    }
                }
            }
        }

        .product-title {
            margin-top: 14px;
            margin-bottom: 0;
            color: $default-dark-color;
        }

        .star-rating {
            display: none; //design
        }
    }

    &.widget_products ul li a {
        position: relative;
        display: block;
    }

    &.widget_shopping_cart {

        .widget_shopping_cart_content {
            @include edgeRelativeHolderLayout();

            ul {

                li {

                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }

                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;

                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }

            .variation {
                @include edgeRelativeHolderLayout();

                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;

                    p {
                        margin: 0;
                    }
                }
            }

            .total {
                margin: 17px 0 13px;
            }

            .buttons {
                margin: 10px 0 0;
            }
        }
    }

    &.widget_layered_nav {

        .edge-widget-title {
            margin: 0 0 17px;
        }

        ul {

            li {
                margin: 2px 0;

                &.chosen {

                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }

    &.widget_layered_nav_filters {

        .edge-widget-title {
            margin: 0 0 17px;
        }
    }

    &.widget_price_filter {

        .price_slider_wrapper {

            .ui-widget-content {
                position: relative;
                height: 1px;
                background-color: $default-dark-color;

                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 10px;
                    width: 10px;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    border-radius: 50%;
                    overflow: hidden;

                    &:last-child {
                        @include edgeTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 24px 0 0;

            .button {
                float: left;
            }

            .price_label {
                float: right;
                font-size: 13px;
                margin-top: 13px;
                color: #000;
            }
        }
    }

    &.widget_product_categories {

	    .edge-widget-title-holder {
		    margin-bottom: 13px;
	    }

        ul {

            li {
                margin: 6px 0;

                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }

    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {

        ul {

            li {
                min-height: 85px;

                a {

                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }

                    span {
                        position: relative;
                        display: block;
                        padding: 20px 0 4px 98px;
                        font-family: $default-heading-font;
                        font-size: 18px;
                        text-transform: uppercase;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }

                > *:not(a) {
                    margin: 0 0 0 98px;
                    line-height: 20px;
                }

                .star-rating {
                    position: relative;

                    span {
                        max-width: 100px;
                    }
                }

                ins {
                    margin: 0;
                }

                del,
                ins {
                    span {
                        color: inherit;
                    }
                }

                ins {
                    text-decoration: none;
                }
            }
        }
    }

    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {

        ul {

            li {
                .amount {
                    font-size: 13px;
                    color: $first-main-color;
                    margin-top: 10px;
                }
            }
        }
    }

    &.widget_recently_viewed_products {

        ul {

            li {

                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }

    &.widget_recent_reviews {

        ul {

            li {

                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }

        a {
            display: block;
            padding: 0 0 0 102px;
        }

        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }

    &.widget_top_rated_products {

        ul {

            li {

                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }

                .amount {
                    display: block;
                }
            }
        }
    }

    &.widget_product_tag_cloud {

        .tagcloud {

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 14px !important;

                &:after {
                    content: '/';
                    color: $default-text-color;
                    margin: 0 0 0 4px;
                }

                &:last-child {

                    &:after {
                        display: none;
                    }
                }
            }
        }
    }

    &.widget_product_search {

        .woocommerce-product-search {
            @include edgeRelativeHolderLayout();
            clear: both;

            > div {
                @include edgeRelativeHolderLayout();
                border-bottom: 2px solid $default-dark-color;
                box-sizing: border-box;
            }

            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 34px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include edgeTransition(color .2s ease-out);
            }

            input {
                width: calc(100% - 20px);
                padding: 0 20px 0 0;
                font-family: inherit;
                font-size: 14px;

                @include edgePlaceholder {
                    color: inherit;
                }
            }

            button {
                width: 20px;
                padding: 3px 0;
                font-size: 16px;
                cursor: pointer;
                color: $default-dark-color;

                @include edgeTransform(scaleX(-1));

                &:hover {
                    color: $default-heading-color;
                }

                span {
                    display: block;
                    line-height: inherit;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */